import React from 'react';
import { Box, Typography, Button, Container } from '@mui/material';
import { motion } from 'framer-motion';

const Hero = ({ data }) => {
  if (!data) return null;

  return (
    <Box
      sx={{
        minHeight: '90vh',
        display: 'flex',
        alignItems: 'center',
        py: 8,
      }}
    >
      <Container>
        <Box
          sx={{
            display: 'flex',
            flexDirection: { xs: 'column', md: 'row' },
            alignItems: 'center',
            gap: 4,
          }}
        >
          <Box sx={{ flex: 1 }}>
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
            >
              <Typography
                variant="h2"
                component="h1"
                gutterBottom
                sx={{ fontWeight: 'bold' }}
              >
                Hi, I'm {data.name}
              </Typography>
            </motion.div>
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.2 }}
            >
              <Typography
                variant="h4"
                color="primary"
                gutterBottom
                sx={{ mb: 3 }}
              >
                {data.role}
              </Typography>
            </motion.div>
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.4 }}
            >
              <Typography variant="body1" color="text.secondary" sx={{ mb: 4 }}>
                {data.description}
              </Typography>
            </motion.div>
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: 0.6 }}
            >
              <Box sx={{ display: 'flex', gap: 2 }}>
                <Button
                  variant="contained"
                  size="large"
                  href="#contact"
                  color="primary"
                >
                  Contact Me
                </Button>
                {data.resumeUrl && (
                  <Button
                    variant="outlined"
                    size="large"
                    href={data.resumeUrl}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    View Resume
                  </Button>
                )}
              </Box>
            </motion.div>
          </Box>
          {data.photo && (
            <motion.div
              initial={{ opacity: 0, scale: 0.5 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{ duration: 0.5, delay: 0.3 }}
              style={{ flex: 1, display: 'flex', justifyContent: 'center' }}
            >
              <Box
                component="img"
                src={data.photo}
                alt={data.name}
                sx={{
                  width: { xs: '80%', sm: '70%', md: '90%' },
                  maxWidth: 400,
                  height: 'auto',
                  borderRadius: '50%',
                  boxShadow: 3,
                }}
              />
            </motion.div>
          )}
        </Box>
      </Container>
    </Box>
  );
};

export default Hero;
